
.footer{
    margin: 50px 0px 0px 0px;
    flex-wrap: wrap;
}

.social__network__protect{
    width: 100px;
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 300;
    font-size: 10px;
    line-height: 14px;
    /* or 140% */

    font-feature-settings: 'pnum' on, 'lnum' on;

    color: #FFFFFF;
}

.info__text{
    font-family: 'Font Awesome 5 Free';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    /* or 167% */

    text-transform: capitalize;
    font-feature-settings: 'pnum' on, 'lnum' on;

    color: #FFFFFF;
}

.material-icons{
    color: #00FF00;
}

.footer__social__network__coloum{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.social__network__icons__row{
    display: flex;
    flex-direction: row;
    padding: 17px 0px 12px 0px;
}

.icon{
    margin: 0px 10px 0px 3px;
}

.info__row{
    display: flex;
}

.footer__info__coloum{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.info__text{
    margin: 0px 0px 0px 10px;
}

.footer__button-up{
    width: 61px;
height: 61px;

background: #00FF00;
border-radius: 30.5px;
}

.button__text{
    margin: 7px 0px 0px 21px;
}
.button__arrow{
    display: flex;
    margin: 8px 0px 0px 19px;
}

@media screen and (max-width: 768px)  {
    .footer__social__network__coloum{
        align-items: center;
        width: 25%;
    }
    .social__network__protect{
        width:unset;
    }
    .clom1{
    }
    .clom3{
    }
    .footer__button-up{
    }
}

@media screen and (max-width:440px) {
    .footer__container{
        flex-wrap: wrap;
    }
    .footer__social__network__coloum{
        order:1;
        margin: 0px 0px 17px -5px;
        width: 100%;
    }
    .footer{
        margin: 50px 0px 0px 0px;
    }
    .info__text{
        margin: 0;
    }
    .clom1{
        order: 2;
    }
    .clom3{
        margin: 11px 0px 0px 0px;
        width: 50%;
        order: 4;
    }
    .clom2{
        order: 3;
    }
    .footer__button-up{
        order: 5;
        margin: 10px 0px 0px -20px;
    }
}

@media screen and (max-width: 380px){
}

@media screen and (max-width:330px){
    .clom3{
        margin: 11px 0px 0px -30px;
    }
}