.content__info{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 41px 0px 0px 0px;
}
.info__other{
    width: 1047px;
    height: 296px;

    background: rgba(255, 255, 255, 0.05);
}

.info__other.active{
    height: 332px;
}

.info__title{
    margin: 15px 0px 0px 32px;
    font-family: 'Bahnschrift';
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 44px;

    text-transform: uppercase;

    color: #00FF00;
}

.form__title{
    padding: 10px 0px 0px 0px;
    margin: 33px 0px 0px 22px;
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
}

.form__text{
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    /* or 125% */
    transform: scale(0);
    transition: 0.5s all;
    color: #FFFFFF;
}

.form__text.active{
    transition: 0.5s all;
    margin: 16px 0px 0px 27px;
    transform: scale(1);
}

.info__form{
    transition: 0.5s all;
    display: block;
    width: 920px;
    height: 43px;
    margin: 0px 0px 0px 62px;
    background: #E5E5E5;
    border-radius: 30px;
}

.info__form.active{
    transition: 0.5s all;
    background-color: #00FF00;
    margin: 0px 0px 74px 62px;
}

.form__circle{
    display: flex;
    align-items: center;
    margin: -21px 0px 0px 868px;
    width: 26px;
    height: 26px;
    background-color: #00FF00;
    border-radius: 50%;
}

.form__circle.active{
    background: #E5E5E5;
}

.form__circle::before{
    content: "";
    margin: 0px 0px 0px 8px;
    display: block;
    width: 11px;
    height: 1px;
    background: #000000;
}

.form__circle::after{
    content: "";
    transition: 0.5s all;
    margin: 0px 0px 0px -11px;
    display: block;
    width: 11px;
    height: 1px;
    background: #000000;
    transform: rotate(90deg);
}

.form__circle.active::after{
    transition: 0.5s all;
    transform: scale(0);
}


