.row__row{
    margin: 0px 0px 0px 21px;
    height: 100%;
    flex-direction: column;
}

.content__registor{ 
    margin: 0 auto;
    display: flex;
    flex-direction: row-reverse;
    background: rgba(255, 255, 255, 0.05);
    width: 1110px;
    height: 658px;
}

.register__blocks{
    margin: 40px 16px;
    width: 600px;
    display: flex;
    justify-content: space-between;
}

.register__block1{
    margin: 0px 16px 0px 0px;
    width: 291px;
    height: 566px;
    background: #FFFFFF;
    border-radius: 10px;
}
.register__block2{
    margin: 0px 20px 0px 0px;
    width: 291px;
    height: 566px;
    background: #FFFFFF;
    border-radius: 10px;
}

.registor__title{
    margin: 40px 0px 12px 0px;
    font-family: 'Bahnschrift';
    font-style: normal;
    font-weight: 700;
    font-size: 52px;
    line-height: 62px;

    text-align: center;

    color: #FFFFFF;
}

.registor__title a{
    color: #00FF00;
    text-decoration: none;
}
.registior__row{
    width: 600px;
    display: flex;
    flex-direction: column;
    align-items: baseline;
}
.registor__undertitle{
    margin: 0 auto;
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 10px;
    /* or 111% */

    text-align: center;

    color: #FFFFFF;
}

.registor__agreement{
    display: flex;
}

ul li div input{
    padding: 0px 0px 0px 40px;
    background: #FFFFFF;
    border-radius: 30px;
    width: 315px;
    height: 41px;
}
.registor__icon{
    position: relative;
    left: -348px;
}

.register__write{
    display: flex;
    align-items: center;
}

.reagistor__ready{
    cursor: pointer;
    width: 352px;
    height: 72px;
    background: #00FF00;
    border-radius: 30px;
    margin: 20px auto;
}
.registor__agreement{
    margin: 0px 66px;
}
.agreement__text{
    margin: 0 33px;
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 5px;
    color: #FFFFFF;
}

.reagistor__info{
    margin: 0 68px;
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 300;
    font-size: 10px;
    line-height: 12px;
    /* or 120% */


    color: #FFFFFF;
}

.reagistor__info a{
    color: #00FF00;
}

.agreement__checkbox{
    appearance: none;
    position: absolute;
}

.agreement__text::before{
    content: "";
    display: block;
    margin: 0 -14px;
    width: 14px;
    height: 14px;
    border: 1px solid #00FF00;
    background:  rgba(255, 255, 255, 0.05);
    position: absolute;
    position: relative;
    z-index: 1;
    top: 19px;
    left: -19px;
    font-feature-settings: 'pnum' on, 'lnum' on;
    border-radius: 5px;
}

.agreement__text::after{
    content: "";
    display: block;
    margin: 0 -14px;
    width: 14px;
    height: 14px;
    background: url('../photo/done.svg') no-repeat;
    background-size: 14px 14px;
    position: relative;
    z-index: 2;
    top: -30px;
    left: -17px;
    opacity: 0;
}

.agreement__checkbox:checked + .agreement__text::before{
    background-color: #00FF00;
}

.agreement__checkbox:checked + .agreement__text::after{
    opacity: 1;
}

@media screen and (max-width: 768px){
    .content__registor{
        width: 661px;
    }
    .register__blocks{
        display: flex;
        justify-content: initial;
        width: 100%;
    }
    .register__block1{
    }
    .register__block2{
    }

    .register__block1,
    .register__block2{
        width: 170px;
        height: 580px;
    }
    .registor__title{
        margin: 40px 0px 12px 65px;
        font-size: 23px;
        line-height: 27px;
    }
    .registor__undertitle{
    font-size: 12px;
    line-height: 14px;
    margin: 0px 0px 0px 88px;
    }
    .register__write{
        width: 250.63px;
        height: 32.62px;
    }
    .reagistor__ready{
        width: 230px;
        height: 70px;
        margin: 20px 0px 0px 71px;
    
    }
    .row__row{
        margin: 0px -21px 0px 31px;
        justify-content: space-evenly;
    }
    .registior__row{
        display: flex;
    flex-direction: column;
    width: 100%;
    }
    .registor__icon{
