@font-face {
	font-family: 'Bahnschrift';
	src: url('../fronts/Bahnschrift/fonts/Bahnschrift.eot');
	src: local('☺'), url('../fronts/Bahnschrift/fonts/Bahnschrift.woff') format('woff'), url('../fronts/Bahnschrift/fonts/Bahnschrift.ttf') format('truetype'), url('../fronts/Bahnschrift/fonts/Bahnschrift.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
    font-family: 'Qanelas';
    src: url('../fronts/Qanelas/Qanelas-Black.eot');
    src: local('Qanelas Black'), local('Qanelas-Black'),
        url('../fronts/Qanelas/Qanelas-Black.eot?#iefix') format('embedded-opentype'),
        url('../fronts/Qanelas/Qanelas-Black.woff') format('woff'),
        url('../fronts/Qanelas/Qanelas-Black.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'Qanelas';
    src: url('../fronts/Qanelas/Qanelas-Black.eot');
    src: local('Qanelas Black'), local('Qanelas-Black'),
        url('../fronts/Qanelas/Qanelas-Black.eot?#iefix') format('embedded-opentype'),
        url('../fronts/Qanelas/Qanelas-Black.woff') format('woff'),
        url('../fronts/Qanelas/Qanelas-Black.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

body{
    margin: 0;
    background: #000000;
}

.header__container{
    display: flex;
    max-width: 1180px;
    margin: 0 auto;
    justify-content: space-around;
}
.content__container{
    max-width: 1180px;
    margin: 0 auto;
}
.footer__container{
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
}

.content{
    flex: 1 1 auto;
}

.icon__header{
    background: url('../photo/icon.svg') 0 0/100% 100% no-repeat;;
    width: 234px;
    height: 33px;
}

.walleper{
    height: 100%;
    width: 100%;
}

.header__icon{
    margin: 22px -245px 0px 0px;
}
.header{
    height: 46px;
}

.content__discounts{
    display: flex;
    margin: 53px auto;
    justify-content: space-between;
    width: 100%;
}

.discounts__text{
    margin: 0px 0px 0px 80px;
    font-family: 'Bahnschrift';
    font-style: normal;
    font-weight: 700;
    font-size: 33px;
    line-height: 25px;

    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
}
.discounts__text a{
    text-decoration: none;
    color: #00FF00;
}
.discounts__start{
    cursor: pointer;
    margin: -12px 202px -1px 0px;
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    text-align: center;

    color: #000000;

    background: #00FF00;
    border-radius: 33.288px;
    width: 288px;
    height: 48px;
}

.question__text{
    margin: 0px 0px 0px 104px;
    font-family: 'Bahnschrift';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 44px;

    text-transform: uppercase;

    color: #FFFFFF;
}

.enroll__btn-text{
    cursor: pointer;
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    text-align: center;

    color: #000000;

    width: 288px;
    height: 48px;

    background: #00FF00;
    border-radius: 33.288px;
}

.content__enroll{
    display: flex;
    justify-content: center;
    margin: 49px 0px;
}

.content__question::before{
    content: '';
    position: absolute;
    left: 260px;
    width: 38px;
    height: 2px;
    background: #00FF00;
}
.content__question::after{
    content: '';
    left: 247px;
    top: 1004px;
    position: absolute;
    width: 27px;
    height: 2px;
    background-color: #00FF00;
    transform: rotate(90deg);
}



@media screen and (max-width: 1345px){
    .discounts__text{
    font-size: 30px;
    }
}

@media screen and (max-width:1260px){
    .discounts__text{
    font-size: 28px;
    }
}

@media screen and (max-width:1200px){
    .discounts__text{
        font-size: 23px;
    }
}

@media screen and (max-width:1110px){
