}

@media screen and (max-width: 440px){
    .header__container{
        max-width: 395px;
    }
    .header__burger{
        position: absolute;
        top: 0;
        display: block;
        width: 38px;
        height: 38px;
        background: #00FF00;
        border-radius: 4px;
    }
    .burger__btn::before,
    .burger__btn::after{
        content: "";
        position: absolute;
        background: #000000;
        border-radius: 4.90323px;
        width: 23.17px;
        height: 2.78px;
    }
    .burger__btn::after{
        bottom: 11px;
        left: 7px;
    }
    .burger__btn::before{
        top:11px;
        left: 7px;
    }
    .content__enroll{
        margin: 49px auto;
    }
    .burger__btn span{
        background: #000000;
        border-radius: 4.90323px;
        width: 23.17px;
        height: 2.78px;
        position: absolute;
        top: 17px;
        left: 7px;
    }
    .header__menu.active{
        position: fixed;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: black;
    }
    .header__icon.mobail.fixed{
        position: fixed;
    }
    ul{
        margin: 75px 0px 0px -32px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    ul li .block__choose{
        width: 300px;
        height: 77px;
    }
    ul li div a{
        display: none;
    }

    ul li div a.active{
        font-size: 24px;
        display: block;
    }

    ul li{
    }


    .icon__header{
        width: 234px;
    height: 33px;
    }
    .header__burger{
        margin: 7px 300px;
    }
    .header__icon.mobail{
        margin: 10px 144px 0px 0px;
    }

    .discounts__start{
    }
    .content__enroll{
    }
    .content__question::before{
        top: 1675px;
        left: 21px;
    }
    .content__question::after{
        left: 9px;
        top: 1689px;
    }

    .header__burger.active{
        position: absolute;
        top:0;
        right: -252px;
    }
    .discounts__text{
        margin: 0px 0px 20px 0px;
    }
    .content__discounts{
        display: flex;
        

    width: 100%;
    align-items: center;
    flex-direction: column;
    }
    .discounts__start{
    }
}

