@font-face {
	font-family: 'Bahnschrift';
	src: url('../fronts/Bahnschrift/fonts/Bahnschrift.eot');
	src: local('☺'), url('../fronts/Bahnschrift/fonts/Bahnschrift.woff') format('woff'), url('../fronts/Bahnschrift/fonts/Bahnschrift.ttf') format('truetype'), url('../fronts/Bahnschrift/fonts/Bahnschrift.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
    font-family: 'Qanelas';
    src: url('../fronts/Qanelas/Qanelas-Black.eot');
    src: local('Qanelas Black'), local('Qanelas-Black'),
        url('../fronts/Qanelas/Qanelas-Black.eot?#iefix') format('embedded-opentype'),
        url('../fronts/Qanelas/Qanelas-Black.woff') format('woff'),
        url('../fronts/Qanelas/Qanelas-Black.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'Qanelas';
    src: url('../fronts/Qanelas/Qanelas-Black.eot');
    src: local('Qanelas Black'), local('Qanelas-Black'),
        url('../fronts/Qanelas/Qanelas-Black.eot?#iefix') format('embedded-opentype'),
        url('../fronts/Qanelas/Qanelas-Black.woff') format('woff'),
        url('../fronts/Qanelas/Qanelas-Black.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

ul li div a{
    margin: 10px 0px 0px 0px;
    font-family: 'Qanelas';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    text-decoration: none;
    color: #FFFFFF;
}

ul li .block__choose{
    display: flex;
    justify-content: center;
    text-align: center;
    width: 155px;
    height: 43px;
}

ul li .block__choose:hover{
    cursor: pointer;
    background: #00FF00;
    border-radius: 50px;
}

ul li{
    list-style-type: none;
}

ul{
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.header__burger{
    display: none;
}

.header__menu{
    width: 551px;
}

